import Link from 'next/link'
import { connect } from 'react-redux'
import Clock from './Clock'
import AddCount from './AddCount'

export default connect(state => state)(
  ({ title, linkTo, lastUpdate, light, articles }) => {
    console.log(articles);
    renderArticlesList = () => {
      return articles.map((article,index) => <div>sf</div>)
  }
    return (
<div className="App">
        <div className="content">
            <div className="page-title">
              <h1 className="title">Articles</h1>
              <hr/>
            </div>
          {this.renderArticlesList()}
        </div>
        <style jsx>
        {` 
        .App {
          text-align: center;
          width: 80%;
          margin: auto;
          color: gray;
          font-family: Arial;
        }
        .content {
          text-align: start;
          color: gray;
          margin-top: 50px;
        
        }
        
        .article-title {
          margin: 0;
        }
        
        .article-date {
          margin: 0;
        }
        
        a {
          text-decoration: none;
        }
        
        .remove-btn {
          color: red;
        }
        .title {
          text-transform: uppercase;
          font-size: 2.2rem;
          text-align: center;
        
        }
        
        .page-title h2 {
          float: left;
        }
        `}
        </style>
        <nav>
          <Link href={linkTo}>
            <a>Navigate</a>
          </Link>
        </nav>
</div>





    )
  }
)
